
.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-decrease,
.cartcontrol .cart-add {
  display: inline-block;
  padding: 6px;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.cartcontrol .cart-decrease .inner,
.cartcontrol .cart-add .inner {
  font-size: 24px;
  line-height: 24px;
  color: #00a0dc;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.cartcontrol .cart-decrease.fadeRotate-enter-active,
.cartcontrol .cart-add.fadeRotate-enter-active,
.cartcontrol .cart-decrease.fadeRotate-leave-active,
.cartcontrol .cart-add.fadeRotate-leave-active {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.cartcontrol .cart-decrease.fadeRotate-enter-active .inner,
.cartcontrol .cart-add.fadeRotate-enter-active .inner,
.cartcontrol .cart-decrease.fadeRotate-leave-active .inner,
.cartcontrol .cart-add.fadeRotate-leave-active .inner {
  display: inline-block;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.cartcontrol .cart-decrease.fadeRotate-enter,
.cartcontrol .cart-add.fadeRotate-enter,
.cartcontrol .cart-decrease.fadeRotate-leave-active,
.cartcontrol .cart-add.fadeRotate-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(24px, 0, 0);
          transform: translate3d(24px, 0, 0);
}
.cartcontrol .cart-decrease.fadeRotate-enter .inner,
.cartcontrol .cart-add.fadeRotate-enter .inner,
.cartcontrol .cart-decrease.fadeRotate-leave-active .inner,
.cartcontrol .cart-add.fadeRotate-leave-active .inner {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.cartcontrol .cart-count {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  padding-top: 6px;
  font-size: 10px;
  color: #93999f;
  line-height: 24px;
  text-align: center;
}
.cartcontrol .cart-add {
  display: inline-block;
  font-size: 24px;
  line-height: 24px;
  padding: 6px;
  color: #00a0dc;
}


