
body,
html {
  line-height: 1;
  font-weight: 200;
  font-family: "PingFang SC", 'STHeitiSC-Light', 'Helvetica', Arial, sans-serif;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px::after -webkit-transform scaleY(0.7) {
    -webkit-transform: scaleY(0.7);
            transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: '.';
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}
@font-face {
  font-family: 'sell-icon';
  src: url("common/fonts/sell-icon.eot?nowozp");
  src: url("common/fonts/sell-icon.eot?nowozp#iefix") format('embedded-opentype'), url("common/fonts/sell-icon.ttf?nowozp") format('truetype'), url("common/fonts/sell-icon.woff?nowozp") format('woff'), url("common/fonts/sell-icon.svg?nowozp#sell-icon") format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"],
[class*=" icon-"] {
/* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'sell-icon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
/* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-arrow_lift:before {
