  content: "\e903";
}
.icon-keyboard_arrow_right:before {
  content: "\e904";
}
.icon-remove_circle_outline:before {
  content: "\e905";
}
.icon-shopping_cart:before {
  content: "\e906";
}
.icon-thumb_down:before {
  content: "\e907";
}
.icon-thumb_up:before {
  content: "\e908";
