
.food-detail {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  z-index: 30;
  width: 100%;
  background: #fff;
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
.food-detail.move-enter-active,
.food-detail.move-leave-active {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.food-detail.move-enter,
.food-detail.move-leave-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.food-detail .food-content .image-header {
  position: relative;
  width: 100%;
  height: 0;
/*设置为100%值时，是相对于盒子的宽度计算的*/
  padding-top: 100%;
}
.food-detail .food-content .image-header img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.food-detail .food-content .image-header .back {
  position: absolute;
  top: 10px;
  left: 0;
}
.food-detail .food-content .image-header .back .icon-arrow_lift {
  display: block;
  padding: 10px;
  font-size: 20px;
  color: #fff;
}
.food-detail .food-content .content {
  padding: 18px;
  position: relative;
}
.food-detail .food-content .content .title {
  line-height: 14px;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
}
.food-detail .food-content .content .detail {
  margin-bottom: 18px;
  line-height: 10px;
  height: 10px;
  font-size: 0;
}
.food-detail .food-content .content .detail .sell-count,
.food-detail .food-content .content .detail .rating {
  font-size: 10px;
  color: #93999f;
}
.food-detail .food-content .content .detail .sell-count {
  margin-right: 12px;
}
.food-detail .food-content .content .price {
  font-weight: 700;
  line-height: 24px;
}
.food-detail .food-content .content .price .now {
  margin-right: 8px;
  font-size: 14px;
  color: #f01414;
}
.food-detail .food-content .content .price .old {
  text-decoration: line-through;
  font-size: 10px;
  color: #93999f;
}
.food-detail .food-content .cartcontrol-wrapper {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.food-detail .food-content .buy {
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 10;
  height: 24px;
  line-height: 24px;
  padding: 0 12px;
  box-sizing: border-box;
  font-size: 10px;
  border-radius: 12px;
  background: #00a0dc;
  color: #fff;
}
.food-detail .food-content .info {
  padding: 18px;
}
.food-detail .food-content .info .title {
  line-height: 14px;
  margin-bottom: 6px;
  font-size: 14px;
  color: #07111b;
}
.food-detail .food-content .info .text {
  line-height: 24px;
  padding: 0 8px;
  font-size: 12px;
  color: #4d555d;
}
.food-detail .food-content .rating {
  padding-top: 18px;
}
.food-detail .food-content .rating .title {
  margin-left: 18px;
  line-height: 14px;
  font-size: 14px;
  color: #07111b;
}


