
.goods {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  width: 100%;
  top: 174px;
  bottom: 46px;
  overflow: hidden;
}
.goods .menu-wrapper {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 80px;
          flex: 0 0 80px;
/*不写80的话安卓手机会出现问题*/
  width: 80px;
  background: #f3f5f7;
}
.goods .menu-wrapper .menu-item {
/*多行文字垂直居中*/
  display: table;
  height: 54px;
  width: 56px;
  line-height: 14px;
  padding: 0 12px;
}
.goods .menu-wrapper .menu-item.current {
  position: relative;
  z-index: 10;
  margin-top: -1px;
  background: #fff;
  font-weight: 700;
}
.goods .menu-wrapper .menu-item.current .text:after {
  display: none;
}
.goods .menu-wrapper .menu-item .text {
  display: table-cell;
  width: 56px;
  vertical-align: middle;
  font-size: 12px;
  position: relative;
}
.goods .menu-wrapper .menu-item .text:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.2);
  content: '';
}
.goods .foods-wrapper {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.goods .foods-wrapper .title {
  padding-left: 14px;
  height: 26px;
  line-height: 26px;
  border-left: 1px solid #d9dde1;
  font-size: 12px;
  color: #93999f;
  background: #f3f5f7;
}
.goods .foods-wrapper .food-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 18px;
  padding-bottom: 18px;
  position: relative;
}
.goods .foods-wrapper .food-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.2);
  content: '';
}
.goods .foods-wrapper .food-item:last-child {
  margin-bottom: 0;
}
.goods .foods-wrapper .food-item:last-child:after {
  display: none;
}
.goods .foods-wrapper .food-item .icon {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 57px;
          flex: 0 0 57px;
  margin-right: 10px;
}
.goods .foods-wrapper .food-item .content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.goods .foods-wrapper .food-item .content .name {
  margin: 2px 0 8px 0;
  height: 14px;
  line-height: 14px;
  font-size: 14px;
  color: #07111b;
}
.goods .foods-wrapper .food-item .content .desc,
.goods .foods-wrapper .food-item .content .extra {
  line-height: 10px;
  font-size: 10px;
  color: #93999f;
}
.goods .foods-wrapper .food-item .content .desc {
  margin-bottom: 8px;
  line-height: 12px;
}
.goods .foods-wrapper .food-item .content .extra .count {
  margin-right: 12px;
}
.goods .foods-wrapper .food-item .content .price {
  font-weight: 700;
  line-height: 24px;
}
.goods .foods-wrapper .food-item .content .price .now {
  margin-right: 8px;
  font-size: 14px;
  color: #f01414;
}
.goods .foods-wrapper .food-item .content .price .old {
  text-decoration: line-through;
  font-size: 10px;
  color: #93999f;
}
.goods .foods-wrapper .food-item .content .cartcontrol-wrapper {
  position: absolute;
  right: 0;
  bottom: 12px;
}


